/*
//voor kaart en tabel
*/
html {
  overflow-y: scroll; }

body {
  background-color: lightSkyBlue;
  font-family: Arial, sans-serif; }

#tekst-boven {
  margin: 0 auto 15px;
  padding: 0 20px;
  border: 2px solid #767676;
  border-radius: 4px;
  max-width: 600px;
  background-color: white; }
  #tekst-boven h2 {
    margin: 10px 0; }

nav {
  display: inline-block; }
  nav a {
    text-align: center;
    text-decoration: none;
    color: white;
    padding: 9px 3px 1px 0;
    margin: 0 10px 15px 0;
    border: 1px solid #DDDDDD;
    border-radius: 4px;
    background-color: gray;
    display: inline-block;
    width: 90px;
    height: 24px;
    cursor: pointer; }

#download {
  font-size: 11px;
  padding: 5px;
  border-radius: 4px;
  display: inline-block;
  float: right;
  width: 60px;
  background-color: #DDDDDD;
  cursor: pointer; }

/*
//kaart
*/
#kaart {
  margin: 0 auto 50px;
  border: 2px solid #767676;
  border-radius: 4px;
  position: relative;
  width: 640px;
  height: 687px;
  background-color: white; }
  @media (max-width: 640px) {
    #kaart {
      width: auto;
      height: auto;
      max-width: 640px;
      max-height: 687px; } }

#pan-rect {
  fill: none;
  pointer-events: all; }

path {
  cursor: pointer;
  fill-opacity: 1;
  stroke: black;
  stroke-width: 0.5; }

path:hover {
  fill-opacity: 0.2; }

.gem-naam {
  text-anchor: middle;
  cursor: pointer;
  pointer-events: none; }

#popupInfo rect {
  fill: white;
  stroke: #767676;
  stroke-width: 1px;
  pointer-events: none; }

#popupInfoText {
  font-size: 8px;
  cursor: pointer;
  pointer-events: none; }

path.ja {
  fill: #42b437; }

path.belangstelling {
  fill: #beda71; }

path.nee {
  fill: #ffe5bc; }

#container.move {
  cursor: move; }

#dialog {
  width: 300px;
  border: 1px solid black;
  border-radius: 4px;
  position: absolute;
  left: 45%;
  top: 45%;
  background-color: white; }
  #dialog h3 {
    padding: 4px 10px;
    background: #ACACAC;
    border-radius: 3px;
    margin: 2px; }

#radio {
  text-align: center;
  margin-top: 20px; }
  #radio div {
    display: inline-block;
    width: 94px;
    padding-bottom: 6px; }
    #radio div.ja.checked {
      border-bottom: 6px solid #42b437; }
    #radio div.belangstelling.checked {
      border-bottom: 6px solid #beda71; }
    #radio div.nee.checked {
      border-bottom: 6px solid #ffe5bc; }
  #radio input {
    cursor: pointer; }

#formButtons {
  display: table;
  border-spacing: 20px;
  margin: 0 auto; }

#formButtons a {
  text-align: center;
  color: white;
  vertical-align: middle;
  padding: 6px 0;
  border: 1px solid #DDDDDD;
  border-radius: 6px;
  display: table-cell;
  width: 90px;
  height: 40px;
  cursor: pointer; }
  #formButtons a:nth-child(1) {
    background-color: #ACACAC; }
  #formButtons a:nth-child(2) {
    background-color: limegreen; }

#formButtons a.disabled {
  background-color: white;
  color: #DDDDDD;
  border-color: #DDDDDD;
  pointer-events: none; }

#zoombuttons, #legenda {
  font-size: 11px;
  padding: 5px;
  border-radius: 2px;
  position: absolute;
  left: 20px; }

#legenda {
  margin-bottom: 15px;
  padding-top: 0;
  padding-bottom: 6px;
  position: absolute;
  bottom: 0;
  width: 330px;
  background-color: white;
  z-index: 991; }
  #legenda .aantal-inwoners {
    position: absolute;
    top: 0;
    left: 155px; }
    #legenda .aantal-inwoners table {
      font-size: 11px; }

.legenda-blokje {
  margin-right: 3px;
  border: solid 1px black;
  position: relative;
  top: 5px;
  width: 18px;
  height: 18px;
  display: inline-block; }
  .legenda-blokje.ja {
    background-color: #42b437; }
  .legenda-blokje.belangstelling {
    background-color: #beda71; }
  .legenda-blokje.nee {
    background-color: #ffe5bc; }

#zoombuttons p {
  position: absolute;
  top: 15px;
  margin: 0;
  padding: 5px;
  width: 125px;
  background-color: white;
  border-radius: 2px; }

#startpositie {
  color: #767676;
  line-height: 1.1;
  text-decoration: none;
  text-align: center;
  padding-top: 4px;
  margin-left: 5px;
  border: 1px solid #ACACAC;
  border-radius: 2px;
  position: absolute;
  top: 60px;
  width: 75px;
  height: 31px;
  background-color: #DDDDDD; }

#startpositie:hover {
  cursor: pointer;
  background-color: #ACACAC;
  border-color: #767676; }

#startpositie.disabled {
  background: white;
  color: #DDDDDD;
  cursor: default; }

/*
//tabel
*/
#table-container {
  margin: 0 auto;
  padding: 10px;
  border: 2px solid gray;
  border-radius: 4px;
  width: 620px;
  background-color: white; }

#zoek {
  margin-bottom: 15px; }
  #zoek input {
    font-size: 16px;
    margin-left: 2px; }
  #zoek .resetbutton {
    color: white;
    line-height: 0;
    padding: 0 0 2px 0;
    border: none;
    border-radius: 8px;
    position: relative;
    top: 0px;
    right: 30px;
    width: 16px;
    height: 16px;
    display: inline-block;
    background-color: #ACACAC;
    cursor: pointer; }

table.data-table {
  border-collapse: collapse;
  margin: 0 auto;
  font-size: 14px; }
  table.data-table th, table.data-table td {
    padding: 0 5px;
    border-bottom: solid 1px #ACACAC; }
  table.data-table th {
    padding: 5px; }
  table.data-table th:nth-child(1) {
    text-align: left; }
  table.data-table td:nth-child(1) {
    border-right: solid 1px #ACACAC;
    width: 220px; }
  table.data-table td:nth-child(3) {
    text-align: center;
    border-left: solid 1px #ACACAC;
    width: 75px; }

#table-container form {
  margin-bottom: 0;
  height: 22px;
  display: inline-block; }

#table-container form:before {
  content: "";
  vertical-align: middle;
  display: inline-block;
  height: 100%; }

div.kleurblokje {
  width: 18px;
  height: 18px;
  margin-right: 15px;
  display: inline-block;
  vertical-align: middle; }
  div.kleurblokje.ja {
    background-color: #42b437; }
  div.kleurblokje.belangstelling {
    background-color: #beda71; }
  div.kleurblokje.nee {
    background-color: #ffe5bc; }

.table-radio {
  display: inline-block;
  vertical-align: middle;
  margin-right: 20px; }
  .table-radio input {
    margin: 0;
    cursor: pointer; }

/*Print styles*/
@media print {
  body {
    -webkit-print-color-adjust: exact;
    print-color-adjust: exact; }

  body * {
    visibility: hidden; }

  #kaart {
    border: none; }

  .ui-resizable-handle {
    background: none; }

  #kaart, #kaart * {
    visibility: visible; }

  #zoombuttons, #zoombuttons * {
    visibility: hidden; }

  /*   #kaart {
      position: absolute;
      left: 0;
      top: 0;
    } */ }

